/* global document, Office, require */

const ssoAuthHelper = require("./../helpers/ssoauthhelper");
let TData;
let ToData;
Office.onReady(info => {
  if (info.host === Office.HostType.Outlook) {
   // Office.context.ui.loa
   $("#authload").show();
   $("#downloadAuth").hide();
   TData = localStorage.getItem("keyDataGet");
   if(TData != "" && TData != null && TData != undefined && TData != "null"){
      startAuthandDownloadP();
   }else{
      ssoAuthHelper.getGraphData();
      startAuthandDownloadP();
   }
    //document.getElementById("getGraphDataButton").onclick = ssoAuthHelper.getGraphData;

  }

  function startAuthandDownloadP(){
    $("#authload").hide();
    $("#downloadAuth").show();
    //$(document).on("click","#loginUser",function(){
     // ssoAuthHelper.getGraphData();
      TData = localStorage.getItem("keyDataGet");
      let intervalCall = setInterval(reocuringFn,2000);
      function reocuringFn(){
        TData = localStorage.getItem("keyDataGet");
        if(TData != "" && TData != null && TData != undefined && TData != "null"){
          clearInterval(intervalCall);
          console.log(localStorage.getItem("keyDataGet"));
          TData = JSON.parse(localStorage.getItem("keyDataGet"));
          if(TData.status == "success"){
            ToData = TData.result;
            let IdD = getItemRestId();
            GetMIMEData(ToData,IdD).then(function (returnData) {
              console.log(returnData);
              if(Office.context.mailbox.item.subject)
              {
                download((Office.context.mailbox.item.subject+".eml"), returnData)
              }else{
                download(("UnNamed.eml"), returnData)
              }
            });
          }
        }
      }
    //})

  }

  function download(filename, text) {
    //var downloadblob = base64toBlob(text);
    var downloadblob = new Blob([text], {
        type: 'text/plain'
    });
    if (window.navigator && window.navigator.msSaveOrOpenBlob) {
        window.navigator.msSaveOrOpenBlob(downloadblob, filename);
        return;
    }
    const data = window.URL.createObjectURL(downloadblob);
    var element = document.createElement('a');
    element.setAttribute('href', data);
    element.setAttribute('download', filename);
    element.style.display = 'none';
    document.body.appendChild(element);
    element.click();
    document.body.removeChild(element);

    Office.context.ui.closeContainer();
}

function getItemRestId() {
  // Currently the only Outlook Mobile version that supports add-ins
  // is Outlook for iOS.
  if (Office.context.mailbox.diagnostics.hostName === 'OutlookIOS') {
      // itemId is already REST-formatted
      return Office.context.mailbox.item.itemId;
  } else {
    // Convert to an item ID for API v2.0
      return Office.context.mailbox.convertToRestId(
          Office.context.mailbox.item.itemId,
          Office.MailboxEnums.RestVersion.v1_0
    );
  }
}

  function GetMIMEData(accessToken,Id) {
    //var d;
    return new Promise(function (resolve, reject) {
        var xhr = $.ajax({
            url: "https://graph.microsoft.com/v1.0/me/messages/'"+encodeURIComponent(Id)+"'/$value",
            type: "Get",
            async: true,
            headers: {
                'Authorization': 'Bearer ' + accessToken,
                //"ACCEPT": "application/json;odata=verbose",
            }
        }).done(function (item) {

            console.log("item");
            console.log(item);

        }).fail(function (error) {
          console.log("itemError");
            console.log(error);
            if (error.status == "401") {
               $("#authload").show();
               $("#downloadAuth").hide();
                localStorage.setItem("keyDataGet","");
                ssoAuthHelper.getGraphData();
                startAuthandDownloadP();
              }
        });

        xhr.done(function (data) {
          resolve(data);
        });
        xhr.fail(function (err) {
            reject(err);

            // loaderClose();
        });

    });
  }
});
